import AppHeader from '../appHeader/AppHeader';
import {BrowserRouter as Router, Routes, Route } from "react-router-dom";
import {AppBanner,
        BreedInfoPage, 
        BreedPage, 
        DislikesPage, 
        FavouritesPage, 
        GaleryPage,
        Page404,
        LikesPage,
        SearchPage, 
        VotingPage} from "../pages";

import '../../style/style.scss';

function App() {

  return (
    <div className="App">
      <Router>
        <AppHeader/>
        <Routes>
            <Route path="https://monticorik.github.io/web-site" element={<AppBanner/>} />
            <Route path="https://monticorik.github.io/web-site/voting" element={<VotingPage/>} />
            <Route path="https://monticorik.github.io/web-site/breed" element={<BreedPage/>} />
            <Route path="https://monticorik.github.io/web-site/breed/:breedId" element={<BreedInfoPage/>} />
            <Route path="https://monticorik.github.io/web-site/galery" element={<GaleryPage/>} />
            <Route path="https://monticorik.github.io/web-site/likes" element={<LikesPage/>} />
            <Route path="https://monticorik.github.io/web-site/favourites" element={<FavouritesPage/>} />
            <Route path="https://monticorik.github.io/web-site/dislikes" element={<DislikesPage/>} />
            <Route path="https://monticorik.github.io/web-site/search/:searchValue" element={<SearchPage/>} />
            <Route path="https://monticorik.github.io/web-site/*" element={<Page404/>} />
        </Routes>
      </Router>
    </div>
  );
}

export default App;